import React, { useState, useCallback, useRef } from 'react';
import { apiClient } from '../services/api-client';
import { useEditorStore } from '../stores/editorStore';
import { useEditor, EditorContent } from '@tiptap/react';
import { Extension } from '@tiptap/core';
import StarterKit from '@tiptap/starter-kit';
import FontFamily from '@tiptap/extension-font-family';
import Underline from '@tiptap/extension-underline';
import TextAlign from '@tiptap/extension-text-align';
import { TextStyle } from '@tiptap/extension-text-style';
import Color from '@tiptap/extension-color';
import Highlight from '@tiptap/extension-highlight';
import './BrowserEditorModal.css';


const FontSize = Extension.create({
  name: 'fontSize',
  addGlobalAttributes() {
    return [{ types: ['textStyle'], attributes: { fontSize: {
      default: null,
      parseHTML: (el: HTMLElement) => el.style.fontSize?.replace('px', '') || null,
      renderHTML: (attrs: any) => attrs.fontSize ? { style: `font-size: ${attrs.fontSize}px` } : {},
    }}}];
  },
  addCommands(): any {
    return {
      setFontSize: (size: string) => ({ chain }: any) => chain().setMark('textStyle', { fontSize: size }).run(),
    };
  },
});

export interface BrowserPage {
  id: string;
  title: string;
  htmlContent: string;
  parentId?: string;
}

interface BrowserEditorModalProps {
  pages: BrowserPage[];
  menuPosition: string;
  menuBgColor: string;
  menuTextColor: string;
  menuFontSize: number;
  contentBgColor: string;
  orientation: string;
  onSave: (pages: BrowserPage[], settings: {
    menuPosition: string;
    menuBgColor: string;
    menuTextColor: string;
    menuFontSize: number;
    contentBgColor: string;
    orientation: string;
  }) => void;
  onClose: () => void;
}

function generateId(): string {
  return Math.random().toString(36).substr(2, 9);
}

interface PageTreeItemProps {
  page: BrowserPage;
  depth: number;
  selectedId: string | null;
  onSelect: (id: string) => void;
  onAddChild: (parentId: string) => void;
  onDelete: (id: string) => void;
  allPages: BrowserPage[];
}

const PageTreeItem: React.FC<PageTreeItemProps> = ({
  page, depth, selectedId, onSelect, onAddChild, onDelete, allPages
}) => {
  const children = allPages.filter(p => p.parentId === page.id);
  return (
    <div>
      <div
        className={`btree-item${selectedId === page.id ? ' selected' : ''}`}
        style={{ paddingLeft: `${8 + depth * 16}px` }}
        onClick={() => onSelect(page.id)}
      >
        <span className="btree-icon">{children.length > 0 ? '' : ''}</span>
        <span className="btree-title">{page.title || ' '}</span>
        <div className="btree-actions">
          <button title=" " onClick={(e) => { e.stopPropagation(); onAddChild(page.id); }}>+</button>
          <button title="" onClick={(e) => { e.stopPropagation(); onDelete(page.id); }}></button>
        </div>
      </div>
      {children.map(child => (
        <PageTreeItem
          key={child.id} page={child} depth={depth + 1}
          selectedId={selectedId} onSelect={onSelect}
          onAddChild={onAddChild} onDelete={onDelete} allPages={allPages}
        />
      ))}
    </div>
  );
};


//     
interface ImageInsertDialogProps {
  pages: BrowserPage[];
  onInsert: (html: string) => void;
  onClose: () => void;
  projectId: string | null;
}

const ImageInsertDialog: React.FC<ImageInsertDialogProps> = ({ onInsert, onClose, projectId }) => {
  const [tab, setTab] = useState<'url' | 'upload'>('url');
  const [url, setUrl] = useState('');
  const [uploading, setUploading] = useState(false);
  const [width, setWidth] = useState('100%');
  const [align, setAlign] = useState<'left' | 'center' | 'right'>('center');
  const fileRef = useRef<HTMLInputElement>(null);

  const alignStyle = (a: string) =>
    a === 'center' ? 'display:block;margin:0 auto;' :
    a === 'right'  ? 'display:block;margin-left:auto;' : '';

  const insertUrl = () => {
    if (!url) return;
    onInsert(`<img src="${url}" style="max-width:${width};${alignStyle(align)}" />`);
  };

  const handleUpload = async (file: File) => {
    if (!projectId) { alert('  '); return; }
    setUploading(true);
    try {
      const result = await apiClient.uploadFile(projectId, file);
      const src = result.url.startsWith('http') ? result.url : `${apiClient.getBaseUrl()}${result.url}`;
      onInsert(`<img src="${src}" style="max-width:${width};${alignStyle(align)}" />`);
    } catch (e: any) {
      alert(' : ' + e.message);
    } finally {
      setUploading(false);
    }
  };

  return (
    <div className="binsert-dialog" onClick={(e) => e.stopPropagation()}>
      <div className="binsert-header">
        <span>  </span>
        <button onClick={onClose}></button>
      </div>
      <div className="binsert-tabs">
        <button className={tab === 'url' ? 'active' : ''} onClick={() => setTab('url')}>URL</button>
        <button className={tab === 'upload' ? 'active' : ''} onClick={() => setTab('upload')}></button>
      </div>
      {tab === 'url' && (
        <div className="binsert-body">
          <input placeholder="https://example.com/image.jpg" value={url}
            onChange={(e) => setUrl(e.target.value)} className="binsert-input" />
          {url && <img src={url} style={{ maxWidth: '100%', maxHeight: '120px', margin: '8px 0' }} />}
        </div>
      )}
      {tab === 'upload' && (
        <div className="binsert-body">
          <input ref={fileRef} type="file" accept="image/*" style={{ display: 'none' }}
            onChange={(e) => { const f = e.target.files?.[0]; if (f) handleUpload(f); }} />
          <button className="binsert-upload-btn" onClick={() => fileRef.current?.click()} disabled={uploading}>
            {uploading ? ' ...' : '  '}
          </button>
        </div>
      )}
      <div className="binsert-options">
        <label>: <input type="text" value={width} onChange={(e) => setWidth(e.target.value)}
          style={{ width: '80px' }} /></label>
        <label>:
          <select value={align} onChange={(e) => setAlign(e.target.value as any)}>
            <option value="left">  </option>
            <option value="center"> </option>
            <option value="right">  </option>
          </select>
        </label>
      </div>
      {tab === 'url' && (
        <div className="binsert-footer">
          <button className="bbtn-primary" onClick={insertUrl} disabled={!url}></button>
        </div>
      )}
    </div>
  );
};

//     
interface GalleryInsertDialogProps {
  onInsert: (html: string) => void;
  onClose: () => void;
  projectId: string | null;
}

const GalleryInsertDialog: React.FC<GalleryInsertDialogProps> = ({ onInsert, onClose, projectId }) => {
  const [images, setImages] = useState<string[]>([]);
  const [urlInput, setUrlInput] = useState('');
  const [type, setType] = useState<'grid' | 'slider'>('grid');
  const [cols, setCols] = useState(3);
  const [uploading, setUploading] = useState(false);
  const fileRef = useRef<HTMLInputElement>(null);

  const addUrl = () => {
    if (urlInput) { setImages(prev => [...prev, urlInput]); setUrlInput(''); }
  };

  const handleUpload = async (files: FileList) => {
    if (!projectId) { alert('  '); return; }
    setUploading(true);
    try {
      const urls: string[] = [];
      for (const file of Array.from(files)) {
        const result = await apiClient.uploadFile(projectId, file);
        const src = result.url.startsWith('http') ? result.url : `${apiClient.getBaseUrl()}${result.url}`;
        urls.push(src);
      }
      setImages(prev => [...prev, ...urls]);
    } catch (e: any) {
      alert(': ' + e.message);
    } finally {
      setUploading(false);
    }
  };

  const buildHtml = () => {
    if (images.length === 0) return;
    const imgs = images.map(src => `<img src="${src}" style="width:100%;height:100%;object-fit:cover;" />`);
    if (type === 'grid') {
      const items = imgs.map(img => `<div style="overflow:hidden;border-radius:4px;">${img}</div>`).join('');
      onInsert(`<div style="display:grid;grid-template-columns:repeat(${cols},1fr);gap:8px;margin:12px 0;">${items}</div>`);
    } else {
      //   CSS + JS
      const id = `slider_${Math.random().toString(36).substr(2,6)}`;
      const slides = imgs.map((img, i) =>
        `<div class="${id}-slide" style="display:${i===0?'block':'none'};width:100%;">${img}</div>`
      ).join('');
      onInsert(`<div id="${id}" style="position:relative;margin:12px 0;overflow:hidden;">
${slides}
<div style="display:flex;justify-content:center;gap:8px;margin-top:6px;">
  <button onclick="(function(){var s=document.querySelectorAll('.${id}-slide'),i=Array.from(s).findIndex(e=>e.style.display!=='none');s[i].style.display='none';s[(i-1+s.length)%s.length].style.display='block'})()"></button>
  <button onclick="(function(){var s=document.querySelectorAll('.${id}-slide'),i=Array.from(s).findIndex(e=>e.style.display!=='none');s[i].style.display='none';s[(i+1)%s.length].style.display='block'})()"></button>
</div>
</div>`);
    }
  };

  return (
    <div className="binsert-dialog" onClick={(e) => e.stopPropagation()}>
      <div className="binsert-header">
        <span>  </span>
        <button onClick={onClose}></button>
      </div>
      <div className="binsert-tabs">
        <button className={type === 'grid' ? 'active' : ''} onClick={() => setType('grid')}></button>
        <button className={type === 'slider' ? 'active' : ''} onClick={() => setType('slider')}></button>
      </div>
      <div className="binsert-body">
        <div style={{ display: 'flex', gap: '6px', marginBottom: '8px' }}>
          <input placeholder="URL " value={urlInput}
            onChange={(e) => setUrlInput(e.target.value)}
            onKeyDown={(e) => e.key === 'Enter' && addUrl()}
            className="binsert-input" style={{ flex: 1 }} />
          <button className="bbtn-primary" onClick={addUrl}>+</button>
        </div>
        <input ref={fileRef} type="file" accept="image/*" multiple style={{ display: 'none' }}
          onChange={(e) => { if (e.target.files) handleUpload(e.target.files); }} />
        <button className="binsert-upload-btn" onClick={() => fileRef.current?.click()} disabled={uploading}>
          {uploading ? ' ...' : '  '}
        </button>
        <div style={{ display: 'flex', flexWrap: 'wrap', gap: '4px', marginTop: '8px' }}>
          {images.map((src, i) => (
            <div key={i} style={{ position: 'relative' }}>
              <img src={src} style={{ width: '60px', height: '60px', objectFit: 'cover', borderRadius: '4px' }} />
              <button onClick={() => setImages(prev => prev.filter((_, j) => j !== i))}
                style={{ position: 'absolute', top: 0, right: 0, background: 'rgba(0,0,0,0.6)', color: '#fff', border: 'none', borderRadius: '50%', width: '16px', height: '16px', cursor: 'pointer', fontSize: '10px', padding: 0 }}></button>
            </div>
          ))}
        </div>
        {type === 'grid' && (
          <div style={{ marginTop: '8px' }}>
            <label style={{ color: '#aaa', fontSize: '12px' }}>: </label>
            <input type="number" min={1} max={6} value={cols}
              onChange={(e) => setCols(Number(e.target.value))}
              style={{ width: '50px', marginLeft: '6px' }} />
          </div>
        )}
      </div>
      <div className="binsert-footer">
        <button className="bbtn-primary" onClick={buildHtml} disabled={images.length === 0}>
            ({images.length} )
        </button>
      </div>
    </div>
  );
};

